
import React from "react"
import { Link } from "gatsby"

const FooterIndex = () => (

<footer style={{
        bottom: `0`,
        position: `absolute`,
        width:`80%`,
        
          }}>
            
            <div style={{
            paddingBottom:`50px`
          }}>
         
            <div  id="footerList" style={{
        marginBottom:`40px`
          }}>
            
            <div><Link to="/" style={{textDecoration: `none`,fontSize: '2vh'}}>
                Home
            </Link></div>
            <div><Link to="/avisolegal" style={{textDecoration: `none`,fontSize: '2vh'}}>
                Aviso Legal
            </Link></div>
            <div><Link to="/politicaprivacidad" style={{textDecoration: `none`,fontSize: '2vh'}}>
                Política de Privacidad
            </Link></div>
            </div> 
            
            <div style={{
            textAlign:`left`,
            float:`left`,
            fontSize:`2vh`
          }}>
            Copyright © {new Date().getFullYear()} cumpleañosamiga
            </div>

            <div id="footerIndex">
            Powered by cumpleañosamiga
            </div>
            </div>
            
        </footer>
        )


        export default FooterIndex