import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import Image from "../image"
import "./headerIndex.css"



const IndexHeader = ({ siteTitle,linksData,disableNavBar}) => {
  function navBar(){
    if(linksData !== undefined){
    linksData.map((item, key) => {
      return(
        <li >
        <h1  class="indexMenu">
          <Link to={item.url} class="headerIndexLink">
            {item.text}
          </Link>
          </h1>
        </li>
      )
    })
  }
  }
  return(
    <>
  <header
    style={{
      position: `relative`
    }}
  >
   
    <div class="headerIndexNavBar" style={disableNavBar ? { visibility: "hidden" } : { visibility: "visible" }}>
     <ul>
     {navBar()}
</ul>
    </div>
    <div class="firstTitleContainer">
        <h2 class="firstTitle">
      El mejor programa para descargar música!</h2>
    </div >
    <div class="secondTitleContainer">
        <h3 class="secondTitle">El mejor programa para descargar musica del 2020 segun tu sistema operativo</h3>
    </div >
    <Image/>
  </header>
  </>
)
  }


IndexHeader.propTypes = {
  siteTitle: PropTypes.string,
}

IndexHeader.defaultProps = {
  siteTitle: ``,
}

export default IndexHeader
